<template>
  <div class="update-name">
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('cancel')"
      @click-right="onConfirm"
    />
    <div class="name-wrap">
      <van-field
        v-model="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="7"
        placeholder=""
        show-word-limit
      />
    </div>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
  name: 'UpdateName',
  components: {},
  props: {
    // props  
    //   obj.name array.push(XX),
    //   obj = {} ,Array= []
    //  v-model = name, 

    //  v-model  value 
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localName: this.value
    }
  },
  computed: {},
  watch: {},
  created () { },
  mounted () { },
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '...',
        forbidClick: true // forbidClick 
      })
      try {
        // 
        await updateUserProfile({
          name: this.localName
        })
        // console.log(data)
        this.$toast.success('')
        //  -> 
        // console.log(this.localName)

        //  input   v-model 
        this.$emit('input', this.localName)
        this.$emit('cancel')
      } catch (error) {
        if (error && error === 409) {
          this.$toast.fail('')
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
.name-wrap {
  padding: 10px;
}
</style>
