<template>
  <div class="user-profile">
    <!--  -->
    <van-nav-bar title="" class="app-nav-bar" left-arrow @click-left="$router.back()"></van-nav-bar>
    <!--  -->
    <!--accept image/jpg .jpg image/png-->
    <input type="file" ref="file" hidden accept="image/*" @change="onFileChange" />
    <van-cell title="" is-link center @click="$refs.file.click()">
      <van-image width="30" height="30" fit="cover" round :src="user.photo" />
    </van-cell>
    <van-cell title="" is-link :value="user.name" @click="isEditNameShow = true"></van-cell>
    <van-cell
      title=""
      is-link
      :value="user.gender === 1 ? '':''"
      @click="isEditGenderShow = true"
    ></van-cell>
    <van-cell title="" is-link :value="user.birthday" @click="isEditBirthShow = true"></van-cell>
    <van-popup v-model="isEditNameShow" position="bottom" :style="{ height: '100%' }">
      <!--
        name
         v-model 
      -->
      <!--
        :name="user.name"
        @update-name="user.name = $event"
      -->
      <!--
         v-model="user.name"
         :value="user.name"  value
         @input="user.name = $event" input
         
         propsvalue
         this.$emit('input', xxx)
         v-model 
      -->
      <!--
           v-model
          , .sync 
           :gender.sync="user.gender"

          :gender="user.gender"
          @update:gender="user.gender = $event"

          
          propsgender
          this.$emit('update:gender', xxx)
      -->
      <!--
          v-model
          .sync
      -->
      <update-name @cancel="isEditNameShow = false" v-model="user.name" v-if="isEditNameShow" />
    </van-popup>

    <van-popup v-model="isEditGenderShow" position="bottom">
      <update-gender
        v-model="user.gender"
        @cancel="isEditGenderShow = false"
        v-if="isEditGenderShow"
      />
    </van-popup>

    <van-popup v-model="isEditBirthShow" position="bottom">
      <update-birthday
        v-model="user.birthday"
        @cancel="isEditBirthShow = false"
        v-if="isEditBirthShow"
      />
    </van-popup>

    <van-popup
      v-model="isEditPhotoShow"
      position="bottom"
      :style="{ height: '100%' }"
      class="update-photo-popup"
    >
      <update-photo
        :file="previewImage"
        @cancel="isEditPhotoShow = false"
        @update-photo="user.photo = $event"
        v-if="isEditPhotoShow"
      />
    </van-popup>
  </div>
</template>

<script>
import { getUserProfile } from '@/api/user'
import UpdateName from './components/update-name'
import UpdateGender from './components/update-gender'
import UpdateBirthday from './components/update-birthday'
import UpdatePhoto from './components/update-photo'
export default {
  name: 'UserProfile',
  components: { UpdateName, UpdateGender, UpdateBirthday, UpdatePhoto },
  props: {},
  data () {
    return {
      user: {},
      isEditNameShow: false, // 
      isEditGenderShow: false, // 
      isEditBirthShow: false, // 
      isEditPhotoShow: false, // 
      previewImage: null // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadUserProfile()
  },
  mounted () { },
  methods: {
    async loadUserProfile () {
      const { data } = await getUserProfile()
      this.user = data.data
      this.user.birthday = '1998-10-01'
    },
    onFileChange () {
      console.log('change')
      // 
      this.isEditPhotoShow = true
      // 
      const file = this.$refs.file.files[0]
      this.previewImage = file
      //  change 
      //  file  value
      this.$refs.file.value = ''
    }
  }
}
</script>

<style scoped lang="less">
.van-popup {
  background-color: #f5f7f9;
}
.update-photo-popup {
  background: #000;
}
</style>
