<template>
  <div class="update-birthday">
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title=""
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="$emit('cancel')"
      @confirm="confirm"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  components: {},
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      minDate: new Date(1900, 0, 1),
      maxDate: new Date(),
      currentDate: new Date(this.value)
    }
  },
  computed: {},
  watch: {},
  created () { },
  mounted () { },
  methods: {
    async confirm (value) {
      this.$toast.loading({
        message: '...',
        forbidClick: true // forbidClick 
      })
      //  YYYY-MM-DD 
      // const data = `${value.getFullYear()}-${value.getMouth() + 1}-${value.getDate()}`
      const data = dayjs(value).format('YYYY-MM-DD')
      // console.log(data)
      // 
      await updateUserProfile({
        birthday: data
      })
      // 
      this.$emit('input', data)
      this.$toast.success('')
      // 
      this.$emit('cancel')
    }
  }
}
</script>

