<template>
  <div class="update-genger">
    <van-picker
      title=""
      show-toolbar
      :columns="columns"
      @confirm="onConfirm"
      @cancel="$emit('cancel')"
      @change="onGenderChange"
      :default-index="gender"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
  name: 'UpdateGenger',
  components: {},
  props: {
    value: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      columns: ['', ''],
      gender: this.value
    }
  },
  computed: {},
  watch: {},
  created () { },
  mounted () { },
  methods: {
    onGenderChange (picker, value, index) {
      this.gender = index
    },
    async onConfirm () {
      if (this.gender === this.value) {
        console.log('...')
        this.$emit('cancel')
        return
      }
      this.$toast.loading({
        message: '...',
        forbidClick: true // forbidClick 
      })
      await updateUserProfile({
        gender: this.gender
      })
      this.$toast.success('')
      this.$emit('input', this.gender)
      this.$emit('cancel')
    }
  }
}
</script>

